.component {
    width: 300px;
}

@media(min-width:1280px) {
    .component .selector {
        width: 800px;
    }

}

@media(min-width:768px) {
    .component {
        width: 600px;
    }

}

@media(min-resolution:192dpi) and (min-width:768px) {
    .component .selector2 {
        background-image: url(/img/retina2x.png);
    }

}
